export default {
	props:{
		itemData:{
			type:Object,
			require:true
		}
	},
	
	data() {
		return {
			defaultUrl: (this.itemData.photoUrl ? this.$api.viewFile(this.itemData.photoUrl) : '../../static/common/default.png')
		}
	},
	methods: {
		// 图片错误
		imageError() {
			this.defaultUrl = '../../static/common/default.png'
		}
	}
}
